@import './variables';
@import './normalize';
@import './headerStyle';
@import './MainStyle';
@import './mixins';

* {
  font-family: 'Nunito', sans-serif;
}

.wrapper {
  padding: 20px;
  width: 100%;
  height: 100vh;

  position: relative;
  overflow: scroll;
  &::-webkit-scrollbar {
    width: 0;
  }
  @media screen and (max-width: 560px) {
    padding: 10px;
  }
  .weather {
    &__container {
      width: 100%;
      max-width: 980px;
      margin: 0 auto;
    }
  }
}

.sceleton {
  width: 100%;
}

.loaded {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  & img {
    width: 100%;
    max-width: 300px;
    height: 100%;
    object-fit: cover;
  }
}

.video {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  z-index: -1;
}
